.Menu{
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    .logo{
        margin-left: 60px;
    }
    .toggler {
        display: none;
        flex-direction: column;
        width: 10%;
        margin: 0;
        @media screen and (max-width:500px) {
            display: flex;
        }
        span {
          display: block;
          width: 25px;
          height: 3px;
          margin-bottom: 5px;
          position: relative;
    
          background: #D0D6F9;
          border-radius: 2px;
    
          z-index: 1;
          cursor: pointer;

        }
      }
    .line{
        background-color: #979797;
        stroke: #ffffff;
        width: 550px;
        height: .5px;
        opacity: .3;
        position: absolute;
        top: 50%;
        left: 12%;
    }
    .nav{
        // background:url("../../assets/Rectangle.png") ;
        
        background-size: cover;
        width: 55%;
        height: 80px;
        margin: 20px 0 20px auto;
        display: flex;
        align-items: center;
        justify-content: center;
        ul{
            list-style-type: none;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 0; 
            margin: 0;
            height: 100%;
            a{
                            text-decoration: none;
                display: flex;
                align-items: center;
                height: 100%;
            }
            li{
                margin: 0 20px;
                color: #ffffff;
                text-transform: uppercase;
                height: 100%;
                display: flex;
                align-items: center;

               
            }
        }
    }
   
}

@media screen and (max-width:800px){
        .Menu{
            // flex-direction: column;
            .logo{
                margin-left: 10px;
            }
            .line{
                display: none;
            }
            .nav{
                background:rgba(#979797 ,.1)!important;
                width: 80%;
                ul{
                    li{
                        margin: 0 10px;
                    }
                }
            }
        }
}

@media screen and (max-width:500px){
    .Menu{
        justify-content: space-between;
        .logo{
            margin-right: 0;
            margin: 20px;
        }
        .nav{
            display: none;
        }
    }
}